module.exports = "@charset \"UTF-8\";\n@media only screen and (max-width: 1068px) {\n  /*для ширины экрана меньше 1068x*/\n  .content {\n    display: grid;\n    grid-template-columns: 10vw 80vw 10vw;\n    grid-template-rows: repeat(6, 1fr);\n    height: unset; }\n  .mngGame {\n    grid-column: 2;\n    grid-row: 3;\n    font-size: 24px;\n    text-align: center;\n    color: black; }\n  .mngedGame {\n    grid-column: 2;\n    grid-row: 6;\n    font-size: 24px;\n    text-align: center;\n    color: black; }\n  /*стиль кнопки для открытия всплывающего окна для добавления игры*/\n  .addGame {\n    text-align: center;\n    grid-column: 2;\n    font-size: 24px;\n    align-self: center;\n    grid-row: 1;\n    color: green;\n    box-sizing: border-box; }\n  .addGame_opener_text {\n    font-size: 18px;\n    line-height: 60px; }\n  .delGame_opener_text {\n    font-size: 18px;\n    line-height: 60px; }\n  .delGame {\n    text-align: center;\n    align-self: center;\n    grid-column: 2;\n    grid-row: 2;\n    color: red;\n    box-sizing: border-box; }\n  /*стиль кнопки для добавления игры в Выбранные игры*/\n  .routeToMngedGame {\n    grid-column: 2;\n    grid-row: 5;\n    text-align: center;\n    align-self: center;\n    color: black; }\n  .routeToMngedGame_btn {\n    height: 60px;\n    width: 60px; }\n  .routeToMngedGame_icon {\n    height: 60px; }\n  /*Input Ввод данных from https://material.angular.io/components/autocomplete/examples-->*/\n  .mngGame-input {\n    grid-column: 2;\n    grid-row: 4;\n    font-size: 18px;\n    text-align: center; }\n  /*стиль карточек выбранных игр*/\n  .cardsOfmngedGames {\n    grid-column: 2;\n    text-align: center;\n    color: red; }\n  /*список выбранных игр*/\n  .pickedGame {\n    color: black;\n    grid-column: 2;\n    grid-row: 7; }\n  .gameNameColumn {\n    width: 90%;\n    text-align: center;\n    float: left; }\n  .filteredgames {\n    font-size: 16px;\n    height: 32px; }\n  h3 {\n    font-size: 18px; }\n  /*кнопки для удаления из выбранных игр*/\n  .unpick {\n    justify-self: start;\n    width: 10%;\n    float: right; }\n  .unpick_btn {\n    height: 45px;\n    width: 45px; }\n  .unpick_icon {\n    height: 45px; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"